<template>
    <v-container>
        <v-timeline side="end" align="start">
            <v-timeline-item
                size="small"
                :dot-color="item.event_color"
                v-for="(item, i) in data"
                :key="i"
            >
                <template v-slot:opposite>
                    <span>{{ item.event_date }}</span>
                </template>
                <div>
                    <h2 :class="`mt-n1 headline font-weight-light mb-4`">
                        {{ item.shop_name }}
                    </h2>
                    <div>{{ item.price.toLocaleString() }}円</div>
                    <v-btn @click="onClickDeleteEvent(item)" color="warning"
                        ><v-icon start icon="mdi-trash-can-outline"></v-icon
                        >削除</v-btn
                    >
                </div>
            </v-timeline-item>
        </v-timeline>
    </v-container>
</template>

<script>
export default {
    name: "TimelineView",
    computed: {
        count() {
            return this.$store.getters.getCount;
        },
    },
    watch: {
        count() {
            console.log("refresh!!");
            this.data = this.getEventList();
            // this.initDialogFlg();
        },
    },
    data: () => ({
        data: [],
    }),
    mounted() {
        this.data = this.getEventList();
    },
    methods: {
        onClickDeleteEvent(e) {
            this.deleteEvent(e);
            this.$store.dispatch("increment");
        },
    },
};
</script>
